@media (min-width: 1200px) {
  .container {
    max-width: 970px;
  }
}
@media (min-width: 400px) {
  .global-search {
    padding: 20px 0;
    text-align: center;
  }
  body {
    padding-top: 15%;
    padding-bottom: 15px;
  }
  .global-search {
    text-align: center;
    padding: 10px;
  }
  .global-search .form-control {
    width: 500px;
  }
  .global-search * {
    display: inline-block;
    margin-bottom: 0;
  }
}
.topcorner {
  position: absolute;
  top: 2%;
  right: 2%;
}
.btn {
  background-color: #4285f4;
  border: none;
}
.step-links a {
  color: #4285f4;
}
